<template>
	<div>
		<!--  -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
		</el-breadcrumb>

		<!--  -->
		<el-card>
			<el-row>
				<el-col :span="8">
					<!--  -->
					<el-input placeholder="">
						<el-button slot="append" icon="el-icon-search"></el-button>
					</el-input>
				</el-col>
			</el-row>

			<!--  -->
			<!-- table -->
			<el-table :data="orderlist" border stripe>
				<!--  -->
				<el-table-column type="index" label="#"></el-table-column>
				<el-table-column prop="order_number" label=""></el-table-column>
				<el-table-column prop="order_price" label=""></el-table-column>
				<el-table-column prop="pay_status" label="">
					<!--   -->
					<template slot-scope="scope">
						<el-tag type="success" v-if="scope.row.pay_status === '1' "></el-tag>
						<el-tag type="danger" v-else></el-tag>
					</template>
				</el-table-column>
				<el-table-column prop="is_send" label=""></el-table-column>
				<el-table-column prop="create_time" label="">
					<!--   -->
					<!--  -->
					<template slot-scope="scope">
						{{scope.row.create_time | dateFormat}}
					</template>
				</el-table-column>
				<el-table-column prop="order_number" label="">
					<!--  -->
					<template>
						<el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"></el-button>
						<el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox"></el-button>
					</template>
				</el-table-column>

			</el-table>

			<!--  -->
			<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[5, 10, 15, 20]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total" background></el-pagination>

			<!--  -->
			<el-dialog title="" :visible.sync="addressVisible" width="50%" @close="addressDialogClosed">
				<el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
					<el-form-item label="/" prop="address1">
						<!--  -->
						<el-cascader v-model="addressForm.address1" :options="cityData" :props="{ expandTrigger: 'hover' }"></el-cascader>
					</el-form-item>
					<el-form-item label="" prop="address2">
						<el-input v-model="addressForm.address2"></el-input>
					</el-form-item>
				</el-form>
				<span slot="footer" class="dialog-footer">
					<el-button @click="dialogVisible = false"> </el-button>
					<el-button type="primary" @click="dialogVisible = false"> </el-button>
				</span>
			</el-dialog>

			<!--  -->
			<el-dialog title="" :visible.sync="progressVisible" width="50%">
				<!--  -->
				<el-timeline>
					<el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.time">
						{{activity.context}}
					</el-timeline-item>
				</el-timeline>

			</el-dialog>

		</el-card>
	</div>

</template>

<script>
	//  citydata.js  
	import cityData from './citydata.js'
	//  db.js  APIdb.js
	import db from './db.js'


	export default {
		data () {
			return {
				// 
				queryInfo: {
					query: '', // 
					pagenum: 1,  // 
					pagesize: 10 // 
				},
				// 
				orderlist: [],
				// 
				total: 0,

				// 
				addressVisible: false,
				// 
				addressForm: {
					address1: [], // 
					address2: ''  // 
				},
				// 
				addressFormRules: {
					address1: [
						{ required: true, message: '/', trigger: 'blur' },
					],
					address2: [
						{ required: true, message: '', trigger: 'blur' },
					],
				},
				//  cityData 
				// cityData: cityData  
				cityData,

				// 
				progressVisible: false,

				// API
				db: db,

				// 
				progressInfo: []


			}
		},

		created () {
			// 
			this.getOrderList()
		},

		methods: {
			//   orders
			async getOrderList () {
				const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
				if (res.meta.status !== 200) {
					return this.$message.error('')
				}

				console.log(res);
				//  data 
				this.total = res.data.total
				this.orderlist = res.data.goods
			},

			//  pageSize 
			handleSizeChange (newSize) {
				this.queryInfo.pagesize = newSize
				this.getOrderList()
			},

			//  pagenum 
			handleCurrentChange (newPage) {
				this.queryInfo.pagenum = newPage
				this.getOrderList()
			},

			// 
			showBox () {
				this.addressVisible = true
			},

			// 
			addressDialogClosed () {
				this.$refs.addressFormRef.resetFields()
			},

			// 
			// () API

			// async showProgressBox () {
			// 	//   '/kuaidi/:id'
			// 	const { data: res } = await this.$http.get('/kuaidi/1106975712662')
			// 	if (res.meta.status !== 200) {
			// 		return this.$message.error('')
			// 	}
			// 	this.progressInfo = res.data

			// 	this.progressVisible = true
			// 	console.log(this.progressInfo)
			// }

			showProgressBox () {
				this.progressInfo = this.db
				this.progressVisible = true
				console.log(this.progressInfo)
			}

		}
	}
</script>

<style lang="less" scoped>
	.el-cascader {
		width: 100%;
	}
</style>